* {
    box-sizing: border-box; }

$color-blue: #5290Ea;
$color-orange: #F28B64;
$color-grey: #E2E2E2;
$color-backgrount-first: #F0F0F0;

html {
    font-family: Montserrat;
    font-weight: 400; }

.container {
    max-width: 1140px;
    margin: 0 auto; }

.main-section {
    position: relative;
    overflow: hidden;
    min-height: 775px;
    background: $color-backgrount-first;
    padding-top: 25px;
    margin-bottom: 39px;
    &__nav-cont {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 114px;
        align-items: center; }
    &__logo {
        width: 216px; }
    &__content-cont {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 100px;
        right: 150px;
        z-index: 1; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 600px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        font-weight: 600;
        &:last-child {
            margin-right: 0; }
        a {
            text-decoration: none;
            color: black; } } }

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 4;
        pointer-events: none; }
    &__rectangle-blue {
        position: absolute;
        top: 120px;
        left: -5px;
        z-index: 2;
        transform: rotate(-63deg);
        width: 565px;
        height: 180px;
        background: $color-blue;
        border-radius: 100px;
        box-shadow: -5px 5px 5px rgba(0, 0, 0, 0.25); }
    &__rectangle-orange {
        position: absolute;
        top: 130px;
        left: 5px;
        z-index: 3;
        transform: rotate(-53deg);
        width: 618px;
        height: 180px;
        background: $color-orange;
        border-radius: 100px;
        box-shadow: -5px 5px 5px rgba(0, 0, 0, 0.25); }
    &__rectangle-grey {
        position: absolute;
        top: 154px;
        left: 35px;
        z-index: 1;
        transform: rotate(-42deg);
        width: 600px;
        height: 180px;
        background: $color-grey;
        border-radius: 100px;
        box-shadow: -5px 5px 5px rgba(0, 0, 0, 0.25); } }


.right-wrapper {
    width: 357px;
    height: 235px;
    position: absolute;
    top: 100px;
    right: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__text {
        font-size: 16px;
        margin-bottom: 5px; }
    z-index: 5; }


.icons {
    display: flex;
    flex-direction: row;
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid $color-blue;
        border-radius: 100px;
        margin-right: 6px;
        z-index: 5;
        a {
            text-decoration: none;
            color: black; }
        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 21px; } } }

.title {
    font-size: 64px;
    position: relative;
    font-weight: 600;
    &__hr {
        width: 65px;
        position: absolute;
        margin: 0;
        bottom: 0;
        border: none;
        border-top: 1px solid black; } }
.hr-new {
    top: 70px; }

.buttons {
    display: flex;
    flex-direction: row;
    &__main-btn {
        margin-right: 20px;
        width: 210px;
        height: 40px;
        background: $color-orange;
        border-radius: 100px;
        border: none;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        font-size: 18px;
        color: white; }
    &__icon-btn {
        display: flex;
        flex-direction: row;
        align-items: center;
        img {
            margin-right: 9px; } }
    &__text {
        font-size: 14px; } }

.elipse {
    width: 156px;
    height: 156px;
    background: linear-gradient(147.71deg, rgba(237, 123, 80, 0.5) 14.08%, rgba(72, 138, 234, 0.5) 51.11%, rgba(44, 44, 44, 0.5) 85.38%);
    border-radius: 100px;
    justify-content: center;
    align-items: center;
    display: flex;
    &_big {
        width: 282px;
        height: 282px;
        border-radius: 200px; }
    &__subtract {
        width: 111.43px;
        height: 111.43px;
        background: $color-backgrount-first;
        border-radius: 100px;
        &_big {
            width: 188px;
            height: 188px; } } }

.second-section {
    position: relative;
    overflow: hidden;
    min-height: 675px;
    background: #FFFFFF;
    margin-bottom: 25px;
    &__title {
        margin-bottom: 25px; }
    &__guitar-block {
        position: relative;
        width: 1140px;
        height: 360px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 53px;
        &_first-elipse {
            position: absolute;
            top: -34px;
            right: -81px;
            z-index: 1; }
        &_second-elipse {
            position: absolute;
            top: 266px;
            left: -63px; } }
    &__boxes {
        position: relative;
        z-index: 2;
        overflow: hidden;
        height: 360px;
        width: 360px;
        border-radius: 10px;
        box-shadow: -5px 5px 5px rgba(0, 0, 0, 0.25);
        &_left {
            background: $color-grey; }
        &_middle {
            background: $color-orange; }
        &_right {
            background: $color-blue; } }
    &__achivments {
        width: 1140px;
        height: 100px;
        display: flex;
        flex-direction: row;
        justify-content: space-between; } }

.box {
    &__content {
        position: relative;
        img {
            position: absolute;
            top: 25px;
            left: 13px;
            margin: 0 0 0 0; }
        &_text {
            position: absolute;
            top: 135px;
            right: 20px;
            font-size: 40px;
            font-weight: 600;
            text-align: center;
            transform: rotate(90deg); } } }

.textmid {
    right: -30px; }

.achiv {
    width: 1140px;
    position: relative;
    h1 {
        position: absolute;
        font-weight: 600;
        font-size: 40px;
        top: 2px;
        left: 117px; }
    p {
        position: absolute;
        font-size: 20px;
        top: 41px;
        left: 112px; }
    img {
        position: absolute; } }

.third-section {
    position: relative;
    overflow: hidden;
    min-height: 675px;
    background: $color-backgrount-first;
    &__content-cont {
        display: flex;
        flex-direction: row; }
    &__left-wrapper {
        position: relative;
        width: 478px;
        height: 314px;
        padding-top: 195px;
        padding-left: 117px;
        margin-right: 238px;
        img {
            position: absolute;
            top: 200px;
            left: 55px; }
        &_elipse {
            position: absolute;
            top: 160px;
            left: 165px; }
        &_title {
            position: absolute;
            z-index: 1;
            top: 263px;
            left: 200px; } }
    &__right-wrapper {
        width: 457px;
        height: 557px;
        padding-top: 97px; }
    &__title-right {
        margin-bottom: 32px; }
    &__text {
        font-size: 20px;
        margin-bottom: 50px; }
    &__second-elipse {
        position: absolute;
        top: -84px;
        right: -103px; } }

.footer {
    position: relative;
    &__img {
        position: absolute;
        left: 00px;
        bottom: -20px; }
    &__container {
        padding-top: 100px;
        min-height: 355px;
        display: flex;
        align-content: center;
        justify-content: flex-end; }
    &__title {
        margin-bottom: 20px; } }


